import moment from "moment";

// Recibo un arreglo de eventos del backend, el cual cada evento tiene dos propiedades (start y end)
// que vienen en string y necesito convertirlo a date

export const prepareEvents = (events = []) => {
  return events.map((each) => ({
    ...each,
    end: moment(each.end).toDate(),
    start: moment(each.start).toDate(),
  }));
};
